body {
  font-family: Arial, Helvetica, sans-serif;
}

.people {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 220px);
  justify-content: center;
  column-gap: 10px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    --columns: 4;
  }
}

.person {
  box-sizing: border-box;
  text-align: center;
  width: 220px;
  padding: 10px 10px 16px;
  transition: transform 1s;
}

.person:hover {
  z-index: 1;
  background-color: #eee;
  border-radius: 12px;
  transform: scale(1.2);
}

.person:hover .photo img {
  border-radius: 60px;
}

.person:hover .extra {
  visibility: inherit;
}

.photo {
  position: relative;
}

.photo > img {
  border-radius: 50%;
  width: 100%;
  height: 100%;
  transition: border-radius 1s;
}

.rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-size: 16px;
  line-height: 32px;
  position: absolute;
  top: 94px;
  right: 10px;
}

.name {
  color: #2f4f4f;
  font-size: 20px;
  line-height: 23px;
}

.extra {
  visibility: hidden;
  margin-top: 10px;
}

.sport:after {
  content: ",";
  font-family: inherit;
}

.pay:before {
  content: "$";
  font-family: inherit;
}

.pay:after {
  content: "M";
  font-family: inherit;
}
